import React, { useState, useEffect } from 'react';
import { Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';


import { character } from '../constants/character';
import { characterUtils, getStatusMax } from '../constants/constantsUtils';
import { CharacterList } from './CharacterList';

import { filterList, filterItemitialize, FilterButtonGroup, IsFilterPassed } from './Filter'
import { SortButton, SortButtonGroup } from './Sort'

const sortItemList = ['実装日', 'HP', '素早さ', '物攻', '物防', '魔攻', '魔防', 'damage0', 'break0', 'heal0', 'wait0', 'damage1', 'break1', 'heal1', 'wait1', 'damage2', 'break2', 'heal2', 'wait2']

characterUtils(character);

const maxStatusValues = getStatusMax(character);







export const CharacterPage = () => {

  const [filterItems, setFilterItems] = useState(filterItemitialize());
  const [sortItem, setSortItem] = useState('実装日');
  const [sortOrder, setSortOrder] = useState('desc');


  useEffect(() => {
    let newRows = [];
    Object.entries(character).forEach(([charName, charEl]) => {
      if (IsFilterPassed({ charEl, filterItems })) {
        newRows.push({
          ...charEl,
          ...charEl.status,
          name: charName,
        })
      }
    })

    newRows.sort((a, b) => {
      const itemA = sortItem === 'date' ? new Date(a[sortItem]) : a[sortItem];
      const itemB = sortItem === 'date' ? new Date(b[sortItem]) : b[sortItem];
      if (sortOrder === 'asc') {
        if (itemA < itemB) return -1; // aがbより前にくる場合
        if (itemA > itemB) return 1;  // bがaより前にくる場合
      } else { // descの場合
        if (itemA < itemB) return 1;  // bがaより前にくる場合
        if (itemA > itemB) return -1; // aがbより前にくる場合
      }
      return 0;  // aとbが同じ場合
    });

    setRows(newRows);
  }, [filterItems, sortItem, sortOrder]);

  const [rows, setRows] = useState([]);





  return (
    <div style={{ padding: '4px' }}>
      <Grid container >
        {Object.keys(filterList).map(group => (
          <Grid item key={group}>
          <FilterButtonGroup filterItems={filterItems} setFilterItems={setFilterItems} group={group} />
        </Grid>

        ))}
      </Grid>
      <Grid container>
        <Grid item>
          <SortButtonGroup props={{ sortItem, setSortItem, sortItemList }} />
        </Grid>
        <Grid item>
          <SortButton props={{ sortOrder, setSortOrder }} />

        </Grid>
      </Grid>


      <CharacterList
        rows={rows}
        maxStatusValues={maxStatusValues}
        filterItems={filterItems}
        sortItem={sortItem}
      />


    </div>
  );
};
