import React, { useState, useEffect } from 'react';
import { Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';
import StarIcon from '@mui/icons-material/Star'

import { skill } from '../constants/skill';
import { ability } from '../constants/ability';
import { gift } from '../constants/gift';

import { Img } from './Img'
import { Star } from './Star'
import { TextBr } from './Text'
import { GiftColor } from './GiftColor'

const width = {
  status: 420,
  skill: 600,
  ability: 200,
  gift: 220,
}

const chipStyle = {
  height: 18,
  fontSize: '12px',
  margin: '1px'
}

const Oval = ({ label }) => {
  const ovalStyle = {
    display: 'inline-flex',
    alignItems: 'center',
    justifyContent: 'center',  // この行を追加
    width: '100%',
    height: '18px',
    fontSize: '12px',
    lineHeight: '18px',
    borderRadius: '9px',
    backgroundColor: lighten('#596782', 0.15),
    color: 'white',
    margin: '1px',
  };

  return (
    <div style={ovalStyle}>
      {label}
    </div>
  );
};

const listItemStyle = (index) => ({
  border: '1px solid lightgray',
  borderTop: index > 0 ? 'none' : '1px solid lightgray',
  borderLeft: 'none',
  borderRight: 'none',
  width: '100%',
  backgroundColor: '#596782',
  padding: 0,
});


const statusStyle = {
  width: 58,
  position: 'relative',
  textAlign: 'right',
  border: '1px solid whitesmoke',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'flex-end',
}

const statusChartStyle = (value, max, isSortItem) => (
  {
    position: 'absolute',
    top: 0,
    left: 0,
    height: '100%',
    width: value ? (value / max * 58) : 0,
    backgroundColor: isSortItem ? 'orange' : 'darkgray'
  }
)

const statusValueStyle = {
  width: 60,
  position: 'relative',
  right: '4px'
}






const statusList = ['HP', '素早さ', '物攻', '物防', '魔攻', '魔防']
const skillStatus = ['damage', 'break', 'heal', 'wait']
const skillStatusMap = {
  damage: 'ダメージ',
  break: 'ブレイク',
  heal: '回復力',
  wait: 'ウェイト',
}

const TooltipOfSkill = ({ skillName }) => {
  return (
    <div>
      <div>{skillName}</div>
      <div><TextBr text={skill[skillName].text} name={skillName} /></div>
    </div>
  )
}

const skillAreaStyle = (isFilterItemArea) => {
  return {
    width: 20,
    minWidth: 20,
    height: 20,
    textAlign: 'center',
    backgroundColor: isFilterItemArea ? 'orange' : 'transparent',  // 'default'を'transparent'に変更しました
  };
}
const splitSortItem = (text) => {
  const result = text.match(/([a-zA-Z]+)(\d+)/);
  if (result) {
    const [, status, index] = result;
    return { status, index };
  }
  return { status: '', index: '' };
};


const SkillStatusView = ({ skillName, skillStatus, sortItem, skillIndex }) => {

  const { status, index } = splitSortItem(sortItem)





  return (
    <div style={{ display: 'flex' }}>
      {skillStatus.map(key => {

        let isSortItem = false
        if (key === status && skillIndex === Number(index)) {
          isSortItem = true
        }

        // console.log(key,status,skillIndex,index,isSortItem)




        return (
          <div key={key} style={{ ...statusStyle }}>
            <div style={statusChartStyle(skill[skillName][key], 500, isSortItem)}></div>
            <div style={statusValueStyle}>
              {
                skill[skillName][key]
                  ? skill[skillName][key].toLocaleString() + (key !== 'wait' ? '%' : '')
                  : '-'
              }
            </div>
          </div>
        )
      })}
    </div>
  );
};




export const CharacterList = ({ rows, maxStatusValues, filterItems, sortItem }) => {


  return (
    <List
      sx={{ width: '100%' }}//, position: 'relative'
      subheader={
        <ListSubheader sx={{ backgroundColor: '#596782', height: 'auto', color: 'white', zIndex: 1, padding: 0, margin: 0, lineHeight: 1 }}>
          <Grid container >
            <Grid item sx={{ width: width.status, alignItems: 'center' }} >
              <Oval label='★はガチャ排出時、ステータスはガチャ詳細画面' />
              <div style={{ display: 'flex' }}>
                <div style={{ width: 60 }}></div>
                {statusList.map((key) => (
                  <div key={key} style={{ width: 60 }}>
                    <Oval label={key} />
                  </div>
                ))}
              </div>
            </Grid>
            <Grid item sx={{ width: width.skill }} >
              <Oval label='スキル' />
              <div style={{ display: 'flex' }}>
                <div style={{ width: 40 }}></div>
                {Object.entries(skillStatusMap).map(([key, text]) => (
                  <div key={key} style={{ width: 60 }}>
                    <Oval label={text} />
                  </div>
                ))}
              </div>
            </Grid>
            <Grid item sx={{ width: width.ability }} >
              <Oval label='アビリティ' />
            </Grid>
            <Grid item sx={{ width: width.gift }} >
              <Oval label='ギフト' />
            </Grid>

          </Grid>
        </ListSubheader>
      }
    >
      {rows.map((charEl, index) => (
        <ListItem key={charEl.name} sx={listItemStyle(index)}  >
          <Grid container sx={{ fontSize: '14px' }}>
            <Grid item sx={{ width: width.status }}>
              <div style={{ display: 'flex', height: 60, }}>
                <div style={{ width: 60 }}>
                  <Img name={charEl.name} square={60} />
                </div>
                <Stack >
                  <div style={{ display: 'flex', alignItems: 'center' }}>
                    <Img name={charEl.role} square={40} />
                    <div style={{ paddingLeft: 4, lineHeight: '1' }} >
                      <div style={{ width: 42 }}>
                        {[...Array(charEl.rarity)].map((_, index) => (
                          <StarIcon key={index} style={{ color: 'yellow', fontSize: '16px', marginRight: -3 }} />
                        ))}
                      </div>
                      {charEl.name.split('_')[0] + '【' + charEl.name.split('_')[1] + '】'}
                    </div>
                  </div>
                  <div style={{ display: 'flex' }}>
                    {Object.entries(charEl.status).map(([key, value]) => {

                      const isSortItem = sortItem === key ? true : false




                      return (
                        <div key={key} style={statusStyle}>
                          <div style={statusChartStyle(value, maxStatusValues[key], isSortItem)}></div>
                          <div style={statusValueStyle}>{value.toLocaleString()}</div>
                        </div>
                      )

                    })}
                  </div>


                </Stack>
              </div>



            </Grid>

            {/* スキル */}
            <Grid item sx={{ width: width.skill }}>
              <div style={{ alignItems: 'center', width: 280 }} >
                {charEl.skill.map((skillName, skillIndex) => {

                  const isFilterItemAttribute = filterItems.attribute.length > 0 && filterItems.attribute.includes(skill[skillName].attribute)
                  const isFilterItemArea = filterItems.area.length > 0 && filterItems.area.includes(skill[skillName].area)


                  return (
                    <Tooltip
                      key={skillName}
                      placement="top-start" arrow
                      title={<TooltipOfSkill skillName={skillName} />}
                    >
                      <div style={{ width: 280, display: 'flex' }}>
                        <div style={skillAreaStyle(isFilterItemAttribute)}>
                          <Img name={skill[skillName].attribute} square={20} rhombus={true} />
                        </div>
                        <div style={skillAreaStyle(isFilterItemArea)}>
                          {skill[skillName].area}
                        </div>
                        <SkillStatusView
                          skillName={skillName}
                          skillStatus={skillStatus}
                          sortItem={sortItem}
                          skillIndex={skillIndex}
                        />
                        {skill[skillName].effects && skill[skillName].effects.map((effectEl) => {
                          return (
                            <Chip
                              key={effectEl.text}
                              label={effectEl.text}
                              size="small"
                              sx={chipStyle}
                            />

                          )
                        })}

                      </div>
                    </Tooltip>
                  )
                })}
              </div>



            </Grid>

            {/* アビリティ */}
            <Grid item sx={{ width: width.ability }}>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: 240, alignItems: 'flex-start' }}>
                {charEl.ability.map(abilityName => {
                  return (
                    <Tooltip key={abilityName}
                      placement="right"
                      arrow
                      title={<TextBr text={ability[abilityName].text} name={abilityName} />}
                    >
                      <Chip
                        label={abilityName}
                        size="small"
                        sx={{ height: 18, fontSize: '12px', margin: '1px' }}
                      />
                    </Tooltip>
                  )

                })}
              </div>
            </Grid>

            {/* ギフト */}
            <Grid item sx={{ width: width.gift, display: 'flex' }}>
              <div style={{ width: 30, height: 60 }}>
                <GiftColor color={charEl.giftColor} />
              </div>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: 240, alignItems: 'flex-start' }}>
                {charEl.gift.map(giftName => {
                  return (
                    <Tooltip
                      key={giftName}
                      placement="right"
                      arrow
                      title={<TextBr text={gift[giftName].text} name={giftName} />}
                    >
                      <Chip
                        icon={<Img name={gift[giftName].type} square={17} borderRadius={8.5} />}
                        label={giftName}
                        size="small"
                        sx={{ height: 18, fontSize: '12px', padding: '1px', margin: '1px' }}
                      />
                    </Tooltip>
                  )
                })}
              </div>
            </Grid>
          </Grid>

        </ListItem>
      ))}
    </List>

  )
}