import { skill } from '../constants/skill';

const roles = ['アタッカー', 'ブレイカー', 'ディフェンダー', 'サポーター']
const attributeResistKeys = ['火', '氷', '風', '雷', '斬', '突', '打']

export const characterUtils = (character) => {

  for (const [charaName, charaEl] of Object.entries(character)) {

    //属性耐性
    for (const key of attributeResistKeys) {
      if (!charaEl.attributeResist[key]) {
        charaEl.attributeResist[key] = 0
      }
    }

    //スキル
    charaEl.attribute = {}
    charaEl.area = {}
    for (const [index, skillName] of charaEl.skill.entries()) {
      charaEl.attribute[skill[skillName].attribute] = true
      charaEl.area[skill[skillName].area] = true
      charaEl['damage' + index] = skill[skillName].damage ?? 0
      charaEl['break' + index] = skill[skillName].break ?? 0
      charaEl['heal' + index] = skill[skillName].heal ?? 0
      charaEl['wait' + index] = skill[skillName].wait ?? 0
    }



  }





}

export const getStatusMax = (character) => {

  const maxStatusValues = {};
  const characterArray = Object.values(character);

  // 初期の最大値オブジェクトを作成します
  const firstCharacter = characterArray[0];
  for (const status in firstCharacter.status) {
    maxStatusValues[status] = firstCharacter.status[status];
  }

  // 他のキャラクターのステータス値と比較します
  for (let i = 1; i < characterArray.length; i++) {
    const character = characterArray[i];
    for (const status in character.status) {
      if (character.status[status] > maxStatusValues[status]) {
        maxStatusValues[status] = character.status[status];
      }
    }
  }

  return (maxStatusValues);  // 各ステータスの最高値を表示します



}